// const classes = useStyles();
class AppNavbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMenu: false,
      isMobile: false,
      mobileStyle: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0
      }
    };
  }

  sideList = (
    <div role="presentation">
      <List>
        <ListItem button>
          <ListItemText
            onClick={() => {
              this.closeMobileMenu();
              Router.push("/okrdoit");
            }}
            primary={"OKRdo.it"}
          />
        </ListItem>
        <Divider />

        {/* <ListItem button>
          <ListItemText
            onClick={() => {
              this.closeMobileMenu();
              Router.push("/kitchenlive");
            }}
            primary={"KitchenLive.tv"}
          />
        </ListItem>
        <Divider /> */}

        <ListItem button>
          <ListItemText
            onClick={() => {
              this.closeMobileMenu();
              Router.push("/sharebooks");
            }}
            primary={"ShareBooks"}
          />
        </ListItem>
        <Divider />
      </List>
    </div>
  );

  componentDidMount() {
    const mql = window.matchMedia("(min-width: 1000px)");
    mql.addListener(this.mediaQueryChanged);
    this.setState({ mql: mql, docked: mql.matches });
    if (window.matchMedia("(min-width: 1000px)").matches) {
      console.log("Over 1000");
      this.setState({
        isMobile: false
      });
    } else {
      console.log("Small 1000");
      this.setState({
        isMobile: true
      });
    }
  }

  componentWillUnmount() {
    this.state.mql.removeListener(this.mediaQueryChanged);
  }

  mediaQueryChanged = () => {
    if (window.matchMedia("(min-width: 1000px)").matches) {
      console.log("Over 1000");
      this.setState({
        isMobile: false
      });
    } else {
      console.log("Small 1000");
      this.setState({
        isMobile: true
      });
    }
    console.log("mediaQueryChanged");
  };

  openMobileMenu = () => {
    this.setState({ isMenu: true });
  };

  closeMobileMenu = () => {
    this.setState({ isMenu: false });
  };

  render() {
    const { classes } = this.props;
    return (
      <div>
        <Head>
          <link
            href="https://fonts.googleapis.com/icon?family=Material+Icons"
            rel="stylesheet"
          />
        </Head>
        <AppBar
          style={{
            background: "#212121",
            borderRadius: 4,
            border: 1,
            palette: {
              background: {
                // default: "#fff"
              }
            }
          }}
        >
          <Toolbar>
            <Typography
              className={classes.title}
              style={{ width: 122, flexGrow: 0 }}
              variant="h6"
              noWrap
              onClick={() => Router.push("/")}
            >
              Ted Works
            </Typography>

            <Typography className={classes.title} variant="h6" noWrap />

            {this.state.isMobile === true ? (
              <Button
                color="inherit"
                variant="outlined"
                className={classes.button}
                onClick={this.openMobileMenu}
              >
                Open Menu
              </Button>
            ) : (
              <>
                <Button
                  color="inherit"
                  variant="outlined"
                  className={classes.button}
                  onClick={() => {
                    Router.push("/okrdoit");
                  }}
                >
                  OKRDO.IT
                </Button>

                {/* <Button
                  color="inherit"
                  variant="outlined"
                  className={classes.button}
                  onClick={() => {
                    Router.push("/kitchenlive");
                  }}
                >
                  Kitchenlive.tv
                </Button> */}

                <Button
                  color="inherit"
                  variant="outlined"
                  className={classes.button}
                  onClick={() => {
                    Router.push("/sharebooks");
                  }}
                >
                  SHARE BOOKS
                </Button>
              </>
            )}

            <SwipeableDrawer
              open={this.state.isMenu}
              onClose={() => this.closeMobileMenu()}
              onOpen={() => this.openMobileMenu()}
            >
              {this.sideList}
            </SwipeableDrawer>
          </Toolbar>
        </AppBar>
        <div style={{ marginTop: "64px", backgroundColor: "red" }} />
      </div>
    );
  }
}

export default withStyles(styles)(AppNavbar);
